
.ui-loading{display:inline-block;width:.53333rem;height:.53333rem;vertical-align:middle;background:transparent url(data:image/svg+xml;base64,) no-repeat;background-size:100%;-webkit-animation:uiLoading 1s steps(12, end) infinite;animation:uiLoading 1s steps(12, end) infinite
}
@-webkit-keyframes uiLoading{
0%{-webkit-transform:rotate3d(0, 0, 1, 0deg);transform:rotate3d(0, 0, 1, 0deg)
}
100%{-webkit-transform:rotate3d(0, 0, 1, 360deg);transform:rotate3d(0, 0, 1, 360deg)
}
}
@keyframes uiLoading{
0%{-webkit-transform:rotate3d(0, 0, 1, 0deg);transform:rotate3d(0, 0, 1, 0deg)
}
100%{-webkit-transform:rotate3d(0, 0, 1, 360deg);transform:rotate3d(0, 0, 1, 360deg)
}
}



