
.ui-footer{padding:.24rem 0;font-size:14px;color:#999;text-align:center
}
.ui-footer_fixed-bottom{position:absolute;left:0;right:0;bottom:0
}
.ui-footer__link{position:relative;display:inline-block;vertical-align:top;margin:0 .24rem
}
.ui-footer__link+.ui-footer__link:after{content:'';position:absolute;top:4px;bottom:4px;left:-.24rem;width:1px;border-left:1px solid rgba(0,0,0,0.14);-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleX(0.5);transform:scaleX(0.5)
}
.ui-footer__text{padding:0 .29333rem;font-size:12px
}



