
.ui-tab{border-style:solid;border-color:rgba(0,0,0,0.14);border-width:1px 0;border-radius:0
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi){
.ui-tab{position:relative;border:none
}
.ui-tab:after{content:"";position:absolute;top:0;left:0;width:200%;height:200%;border-radius:0;border-style:solid;border-color:rgba(0,0,0,0.14);border-width:1px 0;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scale(0.5);transform:scale(0.5);box-sizing:border-box;pointer-events:none
}
}
.ui-tab{position:relative;display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;background-color:#fff
}
.ui-tab__navbar{-webkit-box-flex:1;flex:1;height:1.17333rem;font-size:14px;line-height:1.17333rem;text-align:center
}
.ui-tab__navbar-active{color:#1AAD19
}
.ui-tab__slider{position:absolute;bottom:0;left:0;height:100%;border-bottom:2px solid #1AAD19;-webkit-transition:right 0.2s cubic-bezier(0.35, 0, 0.25, 1) 0.09s,left 0.2s cubic-bezier(0.35, 0, 0.25, 1);transition:right 0.2s cubic-bezier(0.35, 0, 0.25, 1) 0.09s,left 0.2s cubic-bezier(0.35, 0, 0.25, 1)
}
.ui-tab__content{min-height:calc(100% - 1.17333rem)
}
.ui-tab__content.swiper{display:-webkit-box;display:flex
}
.ui-tab__content.swiper>.ui-tab__item{display:block;-webkit-box-flex:1;flex:1
}
.ui-tab__item{display:none;width:100%;height:100%
}
.ui-tab__item-active{display:block
}



