
.ui-swiper{height:100%;overflow:hidden;-webkit-transition:all 0.3s ease;transition:all 0.3s ease
}
.ui-swiper__item{position:relative;float:left;height:100%;background-position:center center;background-size:cover;overflow:hidden
}
.ui-swiper__item.active{-webkit-animation-fill-mode:both;animation-fill-mode:both
}
.ui-swiper__item:not(.active){-webkit-animation:none;animation:none
}



