
.ui-check:checked+.ui-radio:before,.ui-check+.ui-checkbox:before,.ui-check:checked+.ui-checkbox:before{font-family:'icomoon';speak:none;font-style:normal;font-weight:normal;font-variant:normal;text-transform:none;line-height:1
}
.ui-input{width:100%;height:.64rem;padding:0;font-size:inherit;color:inherit;outline:0;background-color:transparent;border:0;overflow-x:scroll;-webkit-appearance:none
}
.ui-input::-webkit-input-placeholder{color:#ddd
}
.ui-textarea{display:block;width:100%;padding:0;font-size:14px;color:inherit;line-height:1.6;border:0;outline:0;overflow-y:scroll;resize:none
}
.ui-textarea::-webkit-input-placeholder{color:#ddd
}
.ui-check-label:active{background-color:rgba(0,0,0,0.08)
}
.ui-check-label_disabled{color:#999
}
.ui-check-label_disabled:active{background-color:transparent
}
.ui-check-label_disabled .ui-check:checked+.ui-checkbox:before{color:rgba(26,173,25,0.5)
}
.ui-check{position:absolute;left:-9999em
}
.ui-check:checked+.ui-radio:before{display:block;content:"\c005";font-size:16px;color:#1AAD19
}
.ui-check+.ui-checkbox:before{content:"\c001";display:block;font-size:20px;color:#bbb
}
.ui-check:checked+.ui-checkbox:before{content:"\c002";display:block;font-size:20px;color:#1AAD19
}
.ui-select{width:100%;min-height:1.17333rem;padding:0 .53333rem 0 .29333rem;font-size:inherit;background-color:transparent;border:0;outline:0;-webkit-appearance:none
}
.ui-switch[type=checkbox]:before,.ui-switch[type=checkbox]:after{content:"";position:absolute;top:0;left:0;height:28px;border-radius:14px;-webkit-transition:-webkit-transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);transition:-webkit-transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);transition:transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);transition:transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35), -webkit-transform 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35)
}
.ui-switch[type=checkbox]{position:relative;width:50px;height:30px;margin:-2px 0;line-height:0;background-color:#dfdfdf;border:1px solid rgba(102,102,102,0.06);border-radius:15px;outline:0;-webkit-appearance:none
}
.ui-switch[type=checkbox]:checked{background-color:#1AAD19
}
.ui-switch[type=checkbox]:checked:before{-webkit-transform:scale(0);transform:scale(0)
}
.ui-switch[type=checkbox]:before{width:48px;background-color:#dfdfdf
}
.ui-switch[type=checkbox]:checked:after{-webkit-transform:translateX(20px);transform:translateX(20px)
}
.ui-switch[type=checkbox]:after{width:28px;background-color:#fff;box-shadow:0 1px 3px rgba(102,102,102,0.6)
}
.ui-count{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;line-height:.64rem;border-radius:.10667rem;overflow:hidden
}
